.tabs {
    margin-top: 2em;
}

.tabs-title {
    color: rgb(161, 219, 161) !important;
}

.tabs-title:active {
    color: green !important;
}

.tabs-title::after {
    color: green !important;
}

.project-container {
    margin: 0 auto;
    margin-top: 20px;
    padding: 10px;
    display: flex;
    justify-content: center;
    max-width: 90%;
}

.project-hero {
    background-color: rgb(36, 36, 36);
    display: flex;
    justify-content: center;
    padding: 15px;
    border-radius: 10px;
    margin-bottom: 20px;
}

.outer-text {
    max-width: 85%;
    text-align: center;
    margin: 0 auto;
    margin-bottom: 20px;
}

.project-display {
    max-width: 60%;
    min-height: auto;
    box-shadow: 3px 3px 10px rgb(27, 27, 27);
    border-radius: 5px;
}

.sub-text {
    display: flex;
    justify-content: space-evenly;
    flex-direction: column;
    margin-left: 30px;
    padding: 20px;
    line-height: 1.8rem;
    background: linear-gradient(to bottom, transparent, rgb(31, 31, 31));
}

h3 {
    text-align: center;
}

.sub-title {
    font-size: 20px;
    text-align: center;
    text-shadow: 3px 3px 6px rgb(24, 24, 24);
}

.gallery {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
    grid-gap: 20px;
    padding: 15px;
}

.project-gallery-img {
    width: 100%;
    height: auto;
}

main {
    background-color: rgb(42, 42, 42);
    border-radius: 10px;
}

.sub-text .button {
    max-width: 150px;
    align-self: center;
    margin-top: 20px;
}

/* Main container styling */
.project-type {
    margin: 20px auto;
    padding: 40px;
    background: linear-gradient(135deg, #1c1c1c 30%, #2a2a2a 100%);
    border-radius: 12px;
    max-width: 700px;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.5);
    border: 1px solid rgba(255, 255, 255, 0.1);
    position: relative;
    overflow: hidden;
}

/* Title styling */
.project-type h5 {
    font-size: 26px;
    color: #f1f1f1;
    margin-bottom: 25px;
    font-family: 'Poppins', sans-serif;
    text-transform: uppercase;
    letter-spacing: 1.5px;
    text-align: center;
    background: linear-gradient(90deg, #33872d 20%, #03652a 80%);
    -webkit-background-clip: text;
    color: transparent;
}

/* List container */
.project-type ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
    z-index: 2;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    align-items: center;
}

/* List items */
.project-type li {
    font-size: 18px;
    color: #e0e0e0;
    margin-bottom: 15px;
    font-family: 'Poppins', sans-serif;
    padding-left: 30px;
    position: relative;
    transition: all 0.3s ease;
    line-height: 1.6;
    margin-left: 10px;
    align-items: center;
}

/* Decorative bullet points */
.project-type li::before {
    content: 'âš’';
    color: #11860d;
    position: absolute;
    left: 0;
    font-size: 22px;
    line-height: 1.6;
}

/* Hover effect for list items */
.project-type li:hover {
    color: #1f9112;
    transform: scale(1.05);
    transition: 0.3s ease-in-out;
}

/* Subtle glowing overlay for background effect */
.project-type::before {
    content: '';
    position: absolute;
    top: -50%;
    left: -50%;
    width: 300%;
    height: 300%;
    background: radial-gradient(circle, rgba(255, 203, 5, 0.1), transparent 60%);
    opacity: 0.1;
    transform: rotate(45deg);
    z-index: 1;
}

.project-type::after {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    width: 250%;
    height: 250%;
    background: radial-gradient(circle, rgba(255, 203, 5, 0.05), transparent 70%);
    opacity: 0.2;
    transform: translate(-50%, -50%);
    z-index: 0;
}

.gallery-title {
    padding-top: 40px;
    margin: 0 auto;
    border-bottom: #116e0f 3px solid;
    max-width: 70%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-bottom: 30px;
}

/* Media Queries */
@media screen and (max-width: 1300px) {
    .project-container {
        flex-direction: column; /* Stack items vertically on smaller screens */
        align-items: center; /* Center items */
    }

    .project-hero {
        flex-direction: column !important; /* Ensure flex direction is column */
        padding: 25px;
        margin-bottom: 20px;
    }

    .project-display {
        box-shadow: 3px 3px 10px rgb(27, 27, 27);
        border-radius: 5px;
        margin: 0 auto;
        align-self: center;
        min-width: 80%;
    }

    .sub-text {
        margin-left: 0px;
        font-size: 14px;
        max-width: 90% !important;
    }

    .spacer{
        height: 20px;
    }

    .sub-text p {
        margin-top: 20px;
    }

    .gallery-title {
        max-width: 80%;
        flex-direction: column;
        margin-bottom: 20px;
        padding: 20px;
        
    }

    
}

@media screen and (min-width: 951px) {
    .project-container {
        flex-direction: row; /* Ensure items are side by side */
    }

    

    .sub-text {
        max-width: 600px;
    }
}

@media (max-width: 900px) {
    .project-type {
        padding: 30px;
    }

    .project-type h5 {
        font-size: 22px;
    }

    .project-type li {
        font-size: 16px;
    }
}

@media (max-width: 900px) {
    .project-container {
        margin: 0 auto;
        max-width: 100%;
        padding: 5px;
    }

    .project-hero {
        max-width: 100%;
        flex-direction: column;
        
    }

    .project-display {
        max-width: 60%;
        max-height: auto;
    }

    .spacer {
        max-width: 100%;
        height: 20px !important;
       
    }
}

@media (max-width: 650px){
    .project-container{
        max-width: 620px;
    }

    .project-hero{
        max-width: 600px;
    }
}
