/* Header Container */
.header-container {
    background-color: #1c1c1c !important; /* Dark grey color */
    color: #ffffff; /* Ensure text is readable */
  }

  .logo{
    max-width: 40px;
    max-height: 40px;
    border-radius: 50px;
  }
  
  /* Adjustments for AppBar */
  .MuiAppBar-root {
    background-color: rgb(45, 45, 45, 0.95) !important; /* Dark grey color */
    color: #ffffff; /* Ensure text and icons are readable */
    position: fixed !important;
    top: 0 !important;
    z-index: 1001 !important;
  }
  
  /* Adjustments for Typography */
  .MuiTypography-root {
    color: #ffffff; /* Ensure text is readable */
  }
  
  /* Adjustments for IconButton */
  .MuiIconButton-root {
    color: #ffffff !important; /* Ensure icons are readable */
  }

  .header-space{
    max-width: 100% !important;
    height: 60px !important;
  }
  