/* General Styling */
.social {
    position: relative;
    padding: 50px;
    text-align: center;
    background: linear-gradient(145deg, #222 30%, #111 90%);
    border-radius: 20px;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.6);
    overflow: hidden;
    color: white;
    max-width: 80%;
    margin: 0 auto;
  }
  
  /* Title and Description Styling */
  .social h3 {
    font-family: 'Poppins', sans-serif;
    font-weight: 600;
    margin-bottom: 15px;
    font-size: 24px;
    color: #f5f5f5;
    text-transform: uppercase;
    letter-spacing: 1px;
  }
  
  .social h4 {
    font-family: 'Poppins', sans-serif;
    font-weight: 400;
    margin-bottom: 30px;
    font-size: 18px;
    color: #d1d1d1;
  }
  
  /* Glowing Borders */
  .social::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 20px;
    background: linear-gradient(45deg, rgba(255, 255, 255, 0.1), rgba(0, 0, 0, 0));
    z-index: 1;
  }
  
  .social::after {
    content: '';
    position: absolute;
    top: -2px;
    left: -2px;
    right: -2px;
    bottom: -2px;
    background: linear-gradient(45deg, rgba(255, 255, 255, 0.05), rgba(255, 255, 255, 0.05));
    border-radius: 20px;
    filter: blur(6px);
    z-index: 0;
  }
  
  /* Icons Styling */
  .icons {
    display: flex;
    justify-content: center;
    gap: 20px;
  }
  
  .icon {
    position: relative;
    font-size: 40px;
    color: #f5f5f5;
    cursor: pointer;
    z-index: 2;
    transition: transform 0.3s ease, color 0.3s ease;
  }
  
  .icon:hover {
    transform: translateY(-5px) scale(1.1);
    color: #ffcb05; /* Subtle glow color */
  }
  
  /* Hover Glow Effect */
  .icon::after {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    width: 80px;
    height: 80px;
    background: radial-gradient(circle, rgba(255, 255, 255, 0.1), transparent);
    border-radius: 50%;
    transform: translate(-50%, -50%) scale(0);
    z-index: -1;
    transition: transform 0.3s ease-in-out;
  }

  
  
  .icon:hover::after {
    transform: translate(-50%, -50%) scale(1.4);
  }
  
  /* Make it responsive */
  @media (max-width: 768px) {
    .social {
      padding: 30px;
    }
  
    .icon {
      font-size: 32px;
    }
  }
  