/* ImageGallery.css */
.my-masonry-grid {
    display: flex;
    margin-left: -15px; /* Adjust the margin */
    width: auto;
    max-width: 80%;
    margin: 0 auto;
}
.my-masonry-grid_column {
    padding-left: 15px; /* Add padding to create gutters */
    background-clip: padding-box;
}

/* Optional: change the order of items */
.my-masonry-grid_column > div {
    margin-bottom: 15px;
}

.gallery-item {
    cursor: pointer;
    overflow: hidden;
    border-radius: 0px;
    transition: transform 0.3s ease-in-out !important;
    position: relative;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.8);
    
}



.gallery-img:hover{
    transform: scale(1.4) !important;
}

.gallery-item.selected {
    transform: scale(1.05); /* Slightly enlarge selected image */
    z-index: 2;
}

.gallery-img {
    width: 100%;
    height: auto;
    object-fit: cover;
    transition: transform 0.3s ease-in-out !important;
}

.lightbox {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background: rgba(0, 0, 0, 0.8);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 10;
}

.lightbox-img {
    max-width: 90%;
    max-height: 90%;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
    border: black 4px solid;
}
