/* Container Styling */
.container {
    background-color: #1e1e1e; /* Dark background */
    color: #f0f0f0; /* Light text color for contrast */
    padding: 40px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3); /* Subtle shadow */
    max-width: 800px;
    margin: 40px auto;
    text-align: left;
    margin-top: 50px;
    
}

/* Title Styling */
.title {
    font-size: 2rem;
    font-weight: 600;
    color: #e6e6e6; /* Slightly lighter than text for emphasis */
    margin-bottom: 20px;
    text-align: center;
    border-bottom: 2px solid #4caf50; /* Accent color for modern look */
    padding-bottom: 10px;
    letter-spacing: 1px;
}

/* Paragraph Styling */
.text-area {
    font-size: 1rem;
    line-height: 1.7;
    margin-bottom: 20px;
    color: #cccccc; /* Slightly lighter for better readability */
    transition: color 0.3s ease;
}

/* Hover Effect */
.text-area:hover {
    color: #ffffff; /* Light up on hover for subtle interaction */
}

/* Link Styling (Optional if you use links inside this component) */
li a {
    color: #4caf50; /* Accent color for links */
    text-decoration: none !important;
    border-bottom: 1px solid #4caf50;
    transition: color 0.3s ease, border-bottom-color 0.3s ease;
}

li a:hover {
    color: #66bb6a;
    border-bottom-color: #66bb6a; /* Lighter shade on hover */
}

/* Responsive Styling */
@media (max-width: 768px) {
    .container {
        padding: 20px;
    }
    
    .title {
        font-size: 1.75rem;
    }

    .text-area {
        font-size: 0.95rem;
    }
}
