/* Hero Section Container */

.hero{
  margin-top: 2em;
}

.button{
  color: #ffffff !important;
  border: 1px solid #ffffff !important;
  transition: transform 0.3s ease !important; 
}

.button:hover{
  color: green !important;
  border: 1.5px solid green !important;
  transform: scale(1.1) !important;
  
}

.hero-container {
    position: relative;
    width: 90%;
    height: 80vh;
    background-image: url('../../images/landscaping/patio_04.jpg'); /* Replace with your image URL */
    background-size: cover;
    background-position: center;
    display: flex;
    margin: 0 auto;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    overflow: hidden; /* Ensures no overflow outside the container */
    box-shadow: 0px 5px 25px 0px #1a1a1a;
  }
  
  /* Gradient Overlay */
  .overlay {
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 40% !important;
    background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.9) 100%);
    backdrop-filter: blur(8px); /* Slight blur for a frosted effect */
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 20px;
    box-sizing: border-box;
    text-align: center;
    flex-direction: column;
  }
  
  /* Overlay Text */
  .overlay-text-name {
    color: #ffffff;
    min-width: 100%;
    font-weight: 900 !important;
    font-size: 3rem !important; /* Adjust as needed for prominence */
    line-height: 1.2; /* Ensure text is not too cramped */
    max-width: 800px; /* Limiting text width for readability */
    z-index: 1;
    border-bottom: 3px solid white;
    margin-bottom: 0.4em !important;
    text-shadow: 2px 2px 8px rgba(0, 0, 0, 0.8); /* Add shadow for better text visibility */
  }
  .overlay-text {
    color: #ffffff;
    font-weight: 700 !important;
    font-size: 1rem !important; /* Adjust as needed for prominence */
    line-height: 1.2; /* Ensure text is not too cramped */
    max-width: 800px; /* Limiting text width for readability */
    z-index: 1;
    text-shadow: 2px 2px 8px rgba(0, 0, 0, 0.7); /* Add shadow for better text visibility */
  }

  .projects-section{
    margin-top: 20px;
    min-width: 100%;
    bottom: 0px;
    display: flex;
    justify-content: space-evenly;
    padding: 25px;
    
  }
  
  /* Responsive Design */
  @media (max-width: 768px) {
    .overlay {
      height: 60%;
    }
  
    .overlay-text {
      font-size: 2rem; /* Adjust font size for smaller screens */
      max-width: 90%; /* Allow more flexibility for smaller screens */
    }
  }
  
  @media (max-width: 480px) {
    .overlay-text {
      font-size: 1.5rem; /* Further adjust font size for very small screens */
    }
  }
  