.footer {
    
    color: #fff;
    padding: 20px;
    text-align: center;
  }
  
  .footer-content {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .footer-nav ul {
    list-style: none;
    padding: 0;
  }
  
  .footer-nav ul li {
    display: inline;
    margin: 0 15px;
  }
  
  .footer-nav ul li a {
    color: #fff;
    text-decoration: none;
  }
  
  .footer-nav ul li a:hover {
    text-decoration: underline;
  }
  
  
  .bottom-footer {
    margin-top: 10px;
    font-size: 0.8rem;
  }
  