/* Container for the masonry grid */
.my-masonry-grid {
    display: flex;
    margin-left: -15px; /* Adjust the margin */
    width: auto;
    max-width: 80%;
    margin: 0 auto;
  }
  
  /* Individual column styles */
  .my-masonry-grid_column {
    padding-left: 15px; /* Add padding to create gutters */
    background-clip: padding-box;
  }
  
  /* Gallery item styles */
  .gallery-item {
    cursor: pointer;
    overflow: hidden;
    border-radius: 0px;
    transition: transform 0.3s ease-in-out;
    position: relative;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.8);
  }
  
  /* Image styles */
  .gallery-img {
    width: 100%;
    height: auto;
    object-fit: cover;
    transition: transform 0.3s ease-in-out;
  }
  
  /* Image hover effect */
  .gallery-img:hover {
    transform: scale(1.05);
  }
  
  /* Lightbox styles */
  .lightbox {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background: rgba(0, 0, 0, 0.8);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 10;
  }
  
  /* Lightbox image styles */
  .lightbox-img {
    max-width: 90%;
    max-height: 90%;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
    border: black 4px solid;
  }
  