.contain{
    background: linear-gradient(to bottom, rgb(44, 44, 44, 0.9), rgb(32, 39, 26));
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2); /* Add subtle shadow for depth */
    border-radius: 15px; /* Rounded corners for smoother design */
}

.container-title{
    color: #ffffff;
    padding-top: 30px;
    font-weight: 800;
    font-size: 26px;
}

.guarantee-container {
    max-width: 100%;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    padding: 20px;
    margin: 30px 0 20px 0;    
}


.guarantee-container h3 {
    font-size: 20px;
    color: #ffffff; /* White text for strong contrast */
    text-align: center;
    margin-bottom: 15px;
    letter-spacing: 1px; /* Slight letter-spacing for readability */
}

.mini-container{
    position: relative;
    margin: 20px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    width: 250px;
    background: linear-gradient(to bottom, rgb(67, 117, 50), rgb(88, 134, 47));
    border-radius: 25px;
    padding: 20px;
    transition: transform 0.3s ease, opacity 0.3s ease;

    
    
}

.mini-container:hover{
    transform: scale(1.05);
}

.png {
    position: relative;
    width: 150px;
    height: 150px;
    margin: 0 auto;   
    filter: brightness(0) invert(1); /* Inverts the image colors to fit dark background */
    opacity: 0.9; /* Slight transparency for a smooth effect */
    transition: transform 0.3s ease, opacity 0.3s ease;
}

.png:hover {
    opacity: 1;
    transform: scale(1.05); /* Slight zoom on hover */
}

p{
    color: #ffffff;
    font-size: 1em;
    line-height: 1.5rem;
    width: 80%;
    text-align: center;
    margin: 0 auto;
    margin-top: 20px;
}

